html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, Helvetica, sans-serif;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.card {
  padding: 0.9375rem;
}

.sidebar {
  width: 100%;
}
@media (min-width: 768px) {
  .sidebar {
    width: 6rem;
  }
}
.sidebar__ul {
  overflow: auto;
}
.sidebar__li a {
  width: 25vw;
}
@media (min-width: 768px) {
  .sidebar__li a {
    width: auto;
    border-bottom: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color) !important;
  }
}

.toggle {
  padding: 0.625rem;
}